.warning{
    background-color: yellow;
    color: black;
    font-size: 20px;
    text-transform: uppercase;
    padding: 10px;
    font-weight: bold;
    border-radius: 4px;
    letter-spacing: 1px;
    width: 100%;
    text-align: center; 
    border-bottom: 4px solid #b5c200;
    border-right: 4px solid #b5c200;
    -webkit-animation-name: loading;
            animation-name: loading;
    -webkit-animation-duration: 3s;
            animation-duration: 3s
}

@-webkit-keyframes loading{
    from{opacity: 0.1;background-color: red;}
    to{opacity: 1;background-color: yellow;}
}

@keyframes loading{
    from{opacity: 0.1;background-color: red;}
    to{opacity: 1;background-color: yellow;}
}


@media screen and (max-width:700px){
    .warning{
        font-size: 15px;
    }
}



